import React, { useContext, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { MyContext } from "./Context";
import image from "./Video/image.svg";
import {FcGoogle} from "react-icons/fc"
function Register() {
  const navigate = useNavigate();
  const [email, setemail] = useState("");
  const [pass, setpass] = useState("");
  const [err2, seterr2] = useState("");
  const { signup,google } = useContext(MyContext);

  const handlegoogle=async()=>{
      try{
          await google()
          navigate("/courses")
      }catch{

      }
  }

  const handlesubmit = async (e) => {
    e.preventDefault();
    try {
      await signup(email, pass);
      setemail("");
      setpass("");
      navigate("/login");
    } catch (error) {
      seterr2(error.message)
      setTimeout(() => {
        seterr2("")
      }, 5000);
    }
  };

  return (
    <>
      <div className="registeration">
        <div className="container">
          <div className="wrapper">
            <h1>Register</h1>
            <h1 className="regiscolor">
              Please fill in this form to create an account
            </h1>

            <form onSubmit={handlesubmit}>
              {/* <input
                placeholder="First Name"
                value={fname}
                required
                onChange={(e) => setfname(e.target.value)}
              /> */}

              <input
                placeholder="Email"
                value={email}
                type="email"
                onChange={(e) => setemail(e.target.value)}
              />
              <input
                placeholder="Password"
                value={pass}
                type="password"
                minLength={6}
                onChange={(e) => setpass(e.target.value)}
              />
              {
                <h2 className={err2 ? "errmsg2" : "errmsg22"}>
                 {err2}
                </h2>
              }

              <button type="submit">Sign up</button>
              <button onClick={handlegoogle}> Continue with &nbsp; &nbsp; <FcGoogle size={"25px"}/> </button> 
            </form>

            <div className="signin">
              Already an existing user? &nbsp; &nbsp;
              <b
                style={{ cursor: "pointer", color: "#4267B2" }}
                onClick={() => {
                  navigate("/login");
                }}
              >
                Login here
              </b>
            </div>
          </div>
        </div>
        <div className="regisimg">
          <img src={image} alt="" />
        </div>
      </div>
    </>
  );
}

export default Register;

