import React, { createContext, useState, useEffect } from "react";
import {auth} from "./Firebase"
import {signInWithEmailAndPassword, createUserWithEmailAndPassword,signOut, onAuthStateChanged, signInWithPopup, GoogleAuthProvider} from "firebase/auth"

export const MyContext = createContext();

function Context({ children }) {
const [user, setuser]= useState("")

  useEffect(()=>{
    const unsub = onAuthStateChanged( auth, e=>{
      setuser(e)
    })
return()=>{
  unsub();
}
  },[])

  console.log("i am auth", auth)

  {user ? console.log("I am user", user.email): console.log("not logged in", user)}
  const signup= (email,pass)=>{
    return createUserWithEmailAndPassword(auth,email,pass)
  }

  const login=(email,pass)=>{
    return signInWithEmailAndPassword(auth, email, pass)
  }

  const logout=()=>{
    return signOut(auth)
  }

  const google=()=>{
    return signInWithPopup(auth, new GoogleAuthProvider())
  }

  return <MyContext.Provider value={{signup, login, logout,user,setuser,google}}>{children}</MyContext.Provider>;
}

export default Context;
