import React, { useContext, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { MyContext } from "./Context";
import image2 from "./Video/image2.svg";
import {FcGoogle} from "react-icons/fc"

function Login() {
  const { login, google } = useContext(MyContext);
  const [email2, setemail2] = useState("");
  const [pass2, setpass2] = useState("");
  const [err1, seterr1] = useState(false);
  const navigate = useNavigate();

const handlegoogle = async()=>{
  try{
    await google()
   navigate("/courses")

}catch{
}}

  const handlesubmit = async (e) => {
    e.preventDefault();
    try {
      await login(email2, pass2);
      navigate("/courses");
      setemail2("");
      setpass2("");
    } catch (error) {
      seterr1(error.message);
      setTimeout(() => {
        seterr1("")
      }, 6000);
    }
  };

  return (
    <>
      <div className="loginregis">
        <div className="loginimg">
          <img src={image2} />
        </div>
        <div className="container2">
          <div className="wrapper2">
            <h1>Login</h1>
            <h1 className="regiscolor">Login to your account</h1>

            <form onSubmit={handlesubmit}>
              <input
                placeholder="Email"
                type="text"
                style={{ flex: "100%" }}
                value={email2}
                onChange={(e) => setemail2(e.target.value)}
              />
              <input
                placeholder="Password"
                type="password"
                value={pass2}
                onChange={(e) => setpass2(e.target.value)}
              />
              <h2 className={err1 ? "errmsg11" : "errmsg1"}>
               {err1}
              </h2>
              <button type="submit">Log in</button>
              <button onClick={handlegoogle}> Continue with &nbsp; &nbsp; <FcGoogle size={"25px"}/> </button> 
            </form>

            <div className="signin">
              Don't have an account? &nbsp; &nbsp;
              <b
                style={{ cursor: "pointer", color: "#4267B2" }}
                onClick={() => {
                  navigate("/register");
                }}
              >
                Register here
              </b>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
